import styled from "styled-components";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import React from "react";

export const RecipeList = styled.div`
  display: flex;
  flex-direction: row;
  padding: 20px;
  gap: 10px;
  justify-content: space-evenly;
  flex-wrap: wrap;
`;

export const CoverImg = styled.img`
  height: 180px;
  object-fit: cover;
  border-radius: 10% 0 0;
`;

export const RecipeName = styled.span`
  font-size: 18px;
  font-weight: 700;
  color: white;
  margin: 10px 0;
`;

export const Ing = styled.span`
  font-size: 18px;
  border: solid 1px #86c6fa;
  border-radius: 5px;
  margin-bottom: 15px;
  cursor: pointer;
  padding: 10px 15px;
  color: #86c6fa;
  text-align: center;
  box-shadow: 0 1px 3px 0 #000;
  font-weight: 600;
`;

export const SomeMore = styled(Ing)`
  color: #d29d2b;
  border: solid 1px #d29d2b;
`;

export const RecipeContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 250px;
  box-shadow: 0 5px 10px 0 #e3eaaa;
  border-radius: 10% 0;
  background-color: #2c3333;
`;

export const Recipe = (props) => {
  const [show, setShow] = React.useState(false);
  const { recipeObj } = props;
  return (
    <React.Fragment>
      <Dialog open={show}>
        <DialogTitle id="alert-dialog-slide-title">Ingredients</DialogTitle>
        <DialogContent>
          <table>
            <thead>
              <th>Ingredients</th>
              <th>Weight</th>
            </thead>
            <tbody>
              {recipeObj.ingredients.map((ingredientObj) => (
                <tr>
                  <td>{ingredientObj.text}</td>
                  <td>{ingredientObj.weight}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </DialogContent>
        <DialogActions>
          <Ing onClick={() => window.open(recipeObj.url)}>See More</Ing>
          <SomeMore onClick={() => setShow(false)}>Close</SomeMore>
        </DialogActions>
      </Dialog>
      <RecipeList>
        <RecipeContainer>
          <CoverImg src={recipeObj.image} />
          <RecipeName>{recipeObj.label}</RecipeName>
          <Ing onClick={() => setShow(true)}>Ingredients</Ing>
          <SomeMore onClick={() => window.open(recipeObj.url)}>
            See Complete Recipe
          </SomeMore>
        </RecipeContainer>
      </RecipeList>
    </React.Fragment>
  );
};
