import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

export const Header = styled.div`
  color: #139487;
  background-color: #fff;
  dispaly: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  font-size: 25px;
  font-weight: 600;
  box-shadow: 0 5px 6px 0 #555;
`;

export const Placeholder = styled.img`
  height: 100px;
  width: 100px;
  margin: 150px;
  opacity: 60%;
`;

export const NameComponent = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
`;

export const AppIcon = styled.img`
  width: 50px;
  height: 50px;
  margin: 10px;
  box-shadow: 0 5px 6px 0 #555;
  border-radius: 0 50%;
  background-color: #139487;
`;

export const Icon = styled.img`
  width: 40px;
  height: 40px;
  margin: 5px;
  box-shadow: 0 5px 6px 0 #555;
  border-radius: 10px;
  cursor: pointer;
`;

export const SearchComponent = styled.div`
  display: flex;
  flex-direction: row;
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  width: 35%;
  background-color: #139487;
`;

export const SearchInput = styled.input`
  border: none;
  outline: none;
  margin-left: 5px;
  font-size: 16px;
  font-weight: 800;
  background-color: #139487;
  color: white;
  width: 100%;
  box-shadow: 0 5px 6px 0 #555;
  border-radius: 5px;
  padding: 10px;
`;
