import Axios from "axios";
import React, { useState } from "react";
import { Recipe, RecipeList } from "./Components/Recipe";
import {
  Container,
  Header,
  NameComponent,
  AppIcon,
  SearchComponent,
  SearchInput,
  Icon,
  Placeholder,
} from "./Components/Header";

export default function App() {
  const APP_ID = "1c976bb9";
  const APP_KEY = "02b3b690aedbd72d9b14cf5a0a40d192";

  const [timeoutId, setTimeoutId] = useState();
  const [recipe, setRecipe] = useState([]);

  const fetch = async (searchString) => {
    const response = await Axios.get(
      `https://api.edamam.com/search?q=${searchString}&app_id=${APP_ID}&app_key=${APP_KEY}`
    );
    setRecipe(response.data.hits);
  };

  const onTextChange = (e) => {
    clearTimeout(timeoutId);
    const timeout = setTimeout(() => fetch(e.target.value), 500);
    setTimeoutId(timeout);
  };

  return (
    <React.Fragment>
      <Container>
        <Header>
          <NameComponent>
            <AppIcon
              src="https://img.icons8.com/bubbles/100/000000/ingredients.png"
              alt="logo"
            />
            Food Recipe Finder
            <SearchComponent>
              <Icon
                src="https://img.icons8.com/color/48/000000/search--v1.png"
                alt="icon"
              />
              <SearchInput
                placeholder="Search Recipe"
                onChange={onTextChange}
              />
            </SearchComponent>
            <div>Home | Call Us | Order now</div>
          </NameComponent>
        </Header>
        <RecipeList>
          {recipe.length ? (
            recipe.map((recipeObj) => <Recipe recipeObj={recipeObj.recipe} />)
          ) : (
            <Placeholder src="https://img.icons8.com/bubbles/100/000000/ingredients.png" />
          )}
        </RecipeList>
      </Container>
    </React.Fragment>
  );
}
